//colors
$primary-text-color: #414a5a;
$second-text-color: #fff;
$third-text-color: #bababa;
$nav-text-color: #484c55;
$buttons-grey-cl: #485460;
$buttons-color-turquoise: #49cbcd;
$input-border-color: #EAEAEA;
$date-cl: #44c0c2;
$link-cl: #a88fd8;

//fonts
$OSRegular: "OpenSansRegular";
$OSSemibold: "OpenSansSemibold";
$OSBold: "OpenSansBold";
$font-RM: "RobotoMedium";
$title-font-size: #{42px}/#{46px};
$primary-font-size: #{13px}/#{17px};
$second-font-size: #{14px}/#{24px};
$primary-heading-font-size: #{26px}/#{30px};
$aside-font-size: #{15px}/#{19px};
$datebox-font-size: #{17px}/#{21px};
.aside-text {
  font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
  color: #757b85;
}

.clr_ln:after {
  content: '';
  clear: both;
  display: table;
  width: 100%;
}

.wrapper_page {
  overflow: hidden;
}

a {
  outline-style: none;
  &:focus {
    outline-style: none;
  }
}

#arrow_up {
  position: fixed;
  cursor: pointer;
  right: 10px;
  bottom: 30px;
  width: 50px;
  height: 50px;
  background: $buttons-color-turquoise;
  color: $second-text-color;
  text-align: center;
  border-right: 0 solid;
  border-radius: 25px;
  padding: 5px 0 0 0;
  z-index: 30;
  &:hover {
    background: #009C9E;
  }
}

#header {
  .container {
    padding-top: 31px;
    padding-bottom: 31px;
  }
}

#logo {
  float: left;
}

.hamburger {
  display: none;
  float: right;
  .line {
    width: 40px;
    height: 5px;
    background-color: $buttons-color-turquoise;
    display: block;
    margin: 8px auto;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  &:hover {
    cursor: pointer;
  }
}

#hamburger.is-active {
  .line:nth-child(1) {
    -webkit-transform: translateY(13px) rotate(45deg);
    -ms-transform: translateY(13px) rotate(45deg);
    -o-transform: translateY(13px) rotate(45deg);
    transform: translateY(13px) rotate(45deg);
  }
  .line:nth-child(2) {
    opacity: 0;
  }
  .line:nth-child(3) {
    -webkit-transform: translateY(-13px) rotate(-45deg);
    -ms-transform: translateY(-13px) rotate(-45deg);
    -o-transform: translateY(-13px) rotate(-45deg);
    transform: translateY(-13px) rotate(-45deg);
  }
}

#nav {
  float: right;
  margin: 10px 0 0 0;
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: 0;
    letter-spacing: 0;
    line-height: 0;
    li {
      display: inline-block;
      vertical-align: top;
      a {
        text-decoration: none;
        color: $nav-text-color;
        padding: 40px 20px 35px 20px;
        font: 15px/19px $OSRegular, Arial, Helvetica, sans-serif;
        &:hover {
          color: $buttons-color-turquoise;
          border-bottom: 3px solid $buttons-color-turquoise;
          background: #f8f9fb;
        }
      }
    }
  }
}

.title_bg {
  margin: 0 auto;
  max-width: 1440px;
  background: url(../img/title_bg.png) top center no-repeat;
  background-size: cover;
}

.title_cont {
  padding: 68px 0 60px;
  margin-bottom: 75px;
}

.title_content {
  text-align: center;
  h1 {
    font: $title-font-size $font-RM, Arial, Helvetica, sans-serif;
    color: $second-text-color;
  }
}

.main_contant{
  margin-bottom: 88px;
}

.post_container {
  margin-bottom: 89px;
}

.post_date {
  float: left;
  padding: 18px;
  background: $buttons-color-turquoise;
  border: 0 solid;
  border-radius: 2px;
  margin: 0 30px 0 0;
  span {
    display: block;
    text-align: center;
  }
  .date_num {
    font: 30px/34px $OSBold, Arial, Helvetica, sans-serif;
    color: $second-text-color;
  }
  .date_mon {
    font: $datebox-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $second-text-color;
  }
  .date_year {
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $second-text-color;
  }
}

.post_box {
  max-width: 731px;
  float: left;
  margin: 0 16px 0 0;
  font: $primary-font-size $OSRegular, Arial, Helvetica, sans-serif;
  line-height: 24px;
  color: $primary-text-color;
  h1 {
    font: $primary-heading-font-size $font-RM, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
    margin: 0 0 26px 0;
  }
  img {
    float: left;
    margin: 0 26px 5px 0;
  }
  p {
    margin: 0 0 15px 0;
  }
  div {
    margin-bottom: 26px;
  }
  aside {
    div {
      padding: 15px 0 13px 0;
      margin-bottom: 0;
      border-top: 1px solid $input-border-color;
      border-bottom: 1px solid $input-border-color;
    }
  }
}

.read_more {
  @extend .aside-text;
  margin: 0 40px 0 0;
  a {
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $link-cl;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
    }
  }
}

.num_comments {
  margin: 0 43px 0 0;
  @extend .read_more;
}

.tags_box {
  margin: 0 31px 0 0;
  @extend .aside-text;
}

.share_set {
  margin: 0 22px 0 0;
  @extend .aside-text;
}

.post_twitter,
.post_facebook {
  margin: 0 14px 0 0;
  .fa-twitter,
  .fa-facebook {
    font-size: 20px;
    color: #dde1e6;
  }

  .fa-twitter {
    &:hover {
      color: #00c3f5;
    }
  }

  .fa-facebook {
    &:hover {
      color: #23599a;
    }
  }
}

.numeration_block {
  position: relative;
  max-width: 737px;
  margin: 0 0 0 11%;
}

.num_navigation {
  .prev-page,
  .next-page {
    position: absolute;
    display: inline-block;
    padding: 3px 15px;
    background: #F5FAFB;
    border: 0 solid;
    border-radius: 20px;
    text-decoration: none;
    color: $third-text-color;
    text-align: center;
    &:hover {
      background: #405360;
      color: $second-text-color;
    }
  }
  .prev-page {
    left: 0;
  }
  .next-page {
    right: 0;
  }
}

.numeration {
  text-align: center;
  a {
    display: inline-block;
    margin: 0 10px 0 0;
    padding: 7px 15px;
    background: #F5FAFB;
    border: 0 solid;
    border-radius: 20px;
    text-decoration: none;
    font: 23px/28px $OSRegular, Arial, Helvetica, sans-serif;
    color: $third-text-color;
    &:hover {
      background: #405360;
      color: $second-text-color;
    }
  }
  .active-button {
    background: #405360;
    color: $second-text-color;
  }
}

.sidebar {
  padding: 18px 0 0 21px;
}

.search {
  position: relative;
  margin: 0 0 60px 0;
  a {
    display: inline-block;
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 10px;
    color: $third-text-color;
  }
}

#search {
  margin: 0 0 0 0;
  input[type="search"] {
    width: 216px !important;
    padding: 15px 35px 15px 18px;
    border: 1px solid #EAEAEA;
    border-radius: 2px;
    font: $second-font-size $OSRegular, Arial, Helvetica, sans-serif;
  }
  input[type="search"]::placeholder {
    color: $third-text-color;
  }
  input[type="search"]::-moz-placeholder {
    color: $third-text-color;
  }
  input[type="search"]::-webkit-input-placeholder {
    color: $third-text-color;
  }
  input[type="search"]:-ms-input-placeholder {
    color: $third-text-color;
  }
  input[type="search"]::-ms-input-placeholder {
    color: $third-text-color;
  }
}
.follow_us{
  h1 {
    margin: 0 0 30px 0;
    font: $primary-heading-font-size $OSRegular, Arial, Helvetica, sans-serif;
    color: $primary-text-color;
  }
}
.follow_us {
  margin: 0 0 46px 0;
  a {
    .fa-facebook,
    .fa-twitter,
    .fa-google-plus {
      color: $second-text-color;
      border: 0 solid;
      border-radius: 20px;
      margin: 0 12px 0 0;
    }
    .fa-facebook {
      background: #23599a;
      padding: 8px 13px;
      &:hover {
        background: #5389ca;
      }
    }
    .fa-twitter {
      background: #00c3f5;
      padding: 8px 9px;
      &:hover {
        background: #20e3ff;
      }
    }
    .fa-google-plus {
      background: #ff4b3e;
      padding: 8px 5px;
      &:hover {
        background: #ff7b6e;
      }
    }
  }
}
.subscribe_box{
  margin-bottom:50px;
  input[type="text"]{
    font: $second-font-size $OSRegular,Arial,Helvetica,sans-serif;
    color: $third-text-color;
    border: 1px solid $input-border-color;
    border-radius: 2px;
    padding: 14px 5px 14px 16px;
    margin: 0 0 30px 0;
    width: 271px !important;
  }
  input[type="text"]::placeholder {
    color: $third-text-color;
  }
  input[type="text"]::-moz-placeholder {
    color: $third-text-color;
  }
  input[type="text"]::-webkit-input-placeholder {
    color: $third-text-color;
  }
  input[type="text"]:-ms-input-placeholder {
    color: $third-text-color;
  }
  input[type="text"]::-ms-input-placeholder {
    color: $third-text-color;
  }
  input[type="submit"]{
    display: inline-block;
    font: 16px/20px $OSRegular,Arial,Helvetica,sans-serif;
    color: $second-text-color;
    background: $buttons-color-turquoise;
    padding: 13px 45px 14px 50px;
    border: 0 solid;
    border-radius: 3px;
    &:hover{
      background: #69ebed;
    }
  }
}
.categories_box{
  margin: 0 0 45px 0;
  @extend .follow_us;
  ul{
    list-style: none;
    padding: 0;
    margin: 0 0 0 20px;
  }
  li::before{
    position: absolute;
    content: '>';
    left: -20px;
    top: 13px;
    line-height: 18px;
    //padding-right:15px;
    font-size: 15px;
    font-weight: bold;
    color: $buttons-color-turquoise;
  }
  li:hover::before{
    color: $second-text-color;
    position: absolute;
    left: 10px;
    top: 13px;
  }
  li{
    position: relative;
    border: 0 solid;
    border-radius: 4px;
    padding: 0 0 0 7px;
    &:hover{
      background: #405360;
      padding-left: 25px;
      a{
        color: $second-text-color;
      }
    }
  }
  a{
    text-decoration: none;
    font: $primary-font-size $OSRegular,Arial,Helvetica,sans-serif;
    color: $primary-text-color;
    padding:12px 0;
    display: inline-block;
  }
}
.popular-post_box{
  margin-bottom: 52px;
  font: 13px/22px $OSRegular,Arial,Helvetica,sans-serif;
  color: $primary-text-color;
  @extend .follow_us;
  img,
  p{
    margin: 0 0 34px 0;
  }
  time{
    font: $second-font-size $OSRegular,Arial,Helvetica,sans-serif;
    color: $primary-text-color;
  }
  a{
    float: right;
    text-decoration: none;
    font: $second-font-size $OSRegular,Arial,Helvetica,sans-serif;
    color: $link-cl;
    &:hover{
      text-decoration: underline;
    }
  }
}
.recent-post_box{
  margin-bottom:53px;
  @extend .follow_us;
  ul{
    padding: 0;
    margin: 0;
    list-style: none;
  }
  li::before{
    content: "\2022";
    padding: 0 17px 0 0;
    color: $buttons-color-turquoise;
    font-size: 30px;
    vertical-align: middle;
  }
  li{
    vertical-align: middle;
    padding: 0 2px;
    //display: inline-block;
  }
  a{
    font: 14px/26px $OSRegular,Arial,Helvetica,sans-serif;
    color: $primary-text-color;
    text-decoration: none;
    display: inline-block;
  }
}
.tags_box{
  @extend .follow_us;
  ul{
    list-style: none;
    padding: 0;
    margin: 0;
  }
  li{
    display: inline-block;
    margin: 0 10px 8px 0;
  }
  a{
    text-decoration: none;
    font: 14px/36px $OSRegular,Arial,Helvetica,sans-serif;
    color: $primary-text-color;
    padding: 8px 15px;
    background: #f8f9fb;
    border: 0 solid;
    border-radius: 4px;
    &:hover{
      background: #00ccce;
      color: $second-text-color;
    }
  }
}
#footer{
  border-top: 1px solid $input-border-color;
  padding: 46px 0 50px 0;
  text-align: center;
  .follow_us{
    margin-bottom: 29px ;
  }
}
.copyright{
  font: $second-font-size $OSRegular,Arial,Helvetica,sans-serif;
  color: #7c7c7c;
  a{
    @extend .copyright;
    text-decoration: none;
    &:hover{
      text-decoration: underline;
    }
  }
}