/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
  .post_container{
    margin-bottom: 70px;
  }
  .post_date{
    margin: 0 0 20px 0;
    padding: 12px 18px;
    span{
      display: inline-block;
    }
    .date_num{
      font: 24px/28px $OSBold, Arial, Helvetica, sans-serif;
      margin: 0 5px 0 0;
    }
  }
  .read_more{
    margin: 0 25px 0 0;
  }
  .sidebar{
    padding: 18px 0 0 0;
  }
  .search{
    a{
      right: 0;
    }
  }
  #search{
    input[type="search"]{
      width: 190px !important;
    }
  }
  .subscribe_box{
    input[type="text"]{
      width: 245px !important;
    }
  }
  .categories_box{
    li::before{
      padding-right: 10px;
    }
    li{
      padding: 0 5px 0 0;
    }
  }
  .recent-post_box{
    li::before{
      //margin: 0 10px 0 0;
    }
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
  .hamburger{
    display: block;
  }
  #nav{
    position: absolute;
    z-index:20;
    top: 77px;
    right: 10px;
    background: #fff;
    margin: 0;
    ul{
      display: none;
      li{
        display: block;
        margin: 0 0 10px 0;
        text-align: center;
        padding: 10px 0;
        &:hover{
          background: #f8f9fb;
          a{
            color: $buttons-color-turquoise;
          }
        }
        a{
          padding: 0 50px;
          &:hover{
            border-bottom: none;
            background: none;
          }
        }
      }
    }
  }
}

